*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color:#EAF0F1;
}
body{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}

.speech{
  margin: 50px auto;
  width: 500px;
}
.group{
  margin: 7px 0;
}
textarea{  
  width: 100%;
  padding: 5px 10px;
  font-size: 15px;
}
h3{
  margin-bottom: 10px;
}
button{
  width: 100%;
  display: block;
  padding: 10px 22px;
  color: #ffff;
  font-weight: bold;
  border-radius: 3px;
  cursor: pointer;
  background-color:#4834DF;

}